.comment-display__actions {
  margin-top: 10px;
  margin-bottom: 5px;
  opacity: 0.6;
  transition: opacity 120ms linear;

  &:hover {
    opacity: 1;
  }
}

.action__icon {
  font-size: 16px;
  color: #6F7E85;
  display: inline-block;

  svg {
    width: 12px;
    height: 12px;
    fill: #384348;
    display: inline-block;
    vertical-align: middle;

    #icon-shape {
      fill: #384348;
    }
  }
}